import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { AuthenticationGuard } from 'core/services/authentication.service';
import { AuthorizationGuard } from 'core/services/authorization.service';
import { QrCodeComponent } from './qr-code/qr-code.component';
import { QuestionAnswerComponent } from './question-answer/question-answer.component';
import { RecoveryCodesComponent } from './recovery-codes/recovery-codes.component';

const routes: Routes = [
  {
    path: '',
    data: {
      title: 'Multi-factor'
    },
    children: [
      {
        path: 'qr-code',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Authenticator App',
          access: {
            module: 'user',
            entity: 'mfa',
            action: 'enable'
          }
        },
        component: QrCodeComponent
      },
      {
        path: 'question-answer',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Question & Answer',
          access: {
            module: 'user',
            entity: 'mfa',
            action: 'enable'
          }
        },
        component: QuestionAnswerComponent
      },
      {
        path: 'recovery-codes',
        canActivate: [AuthenticationGuard, AuthorizationGuard],
        data: {
          title: 'Recovery Codes',
          access: {
            module: 'user',
            entity: 'mfa',
            action: 'enable'
          }
        },
        component: RecoveryCodesComponent
      }
    ]
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class MultiFactorRoutingModule { }
