import { OnInit, Component, ViewEncapsulation, ViewChild } from '@angular/core';
import { ProfileService, Question, Answer } from '../../profile/profile.service';
import { UserService } from '../../user.service';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { uniq } from 'lodash';
import { MFA } from 'config/constants/user';

@Component({
  selector: 'app-question-answer',
  templateUrl: 'question-answer.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [ProfileService, UserService]
})
export class QuestionAnswerComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  profile_mfa: boolean = false;
  reconfigure: boolean = false;

  questions: Array<Question>;
  answers: Array<Answer>;
  question_loading: boolean = false;

  message: string;
  question_error: string;

  constructor(private profileService: ProfileService, private userService: UserService) {

    this.answers = new Array<Answer>(5);
    for (let i = 0; i < 5; i++) {
      this.answers[i] = new Answer();
    }
  }

  ngOnInit(): void {

    const self = this;

    this.initiate();
    this.profileService.getProfile('mfa').then((response) => {

      if (response.mfa) {
        for (const mfa of response.mfa) {
          switch (mfa.mfa_type) {
            case MFA.QUESTION_ANSWER:

              self.profile_mfa = true;
              mfa.questions.forEach(function (question, i) {
                self.answers[i].question_id = question.question_id;
                self.answers[i].answer = '**********';
              });
              break;
          }
        }
      }
    });
  }

  private __validateQuestions() {

    const question_ids = [],
      answers = [];
    let result = true;
    this.question_error = '';

    this.answers.forEach(function(answer) {

      if (answer.question_id) {
        question_ids.push(answer.question_id);
      }

      if (answer.answer) {
        answers.push(answer.answer);
      }
    });

    if (uniq(question_ids).length !== 5) {

      this.question_error = 'Select 5 unique security questions';
      result = false;

    } else if (answers.length !== 5) {

      this.question_error = 'Select 5 security questions and provide answer for all';
      result = false;
    }

    return result;
  }

  initiate() {

    const self = this;
    this.question_loading = true;

    this.userService.initiateMfa(MFA.QUESTION_ANSWER).then((response) => {

      self.questions = response.questions;
      self.question_loading = false;

    }).catch((err) => {

      self.message = err.error.message.text;
      self.errorModal.show();
    });
  }

  activate() {

    if (!this.__validateQuestions()) {
      return;
    }
    const data: any = {
      mfa_type: MFA.QUESTION_ANSWER,
      questions: this.answers
    };

    this.userService.enableMfa(data).then((response) => {

      this.profile_mfa = true;
      this.reconfigure = false;
      this.answers.forEach(function (answer) {
        answer.answer = '**********';
      });

      this.message = response.message.text;
      this.successModal.show();

    }).catch((err) => {

      this.message = err.message.text;
      this.errorModal.show();
    });
  }

  deactivate() {

    const data: any = {
      mfa_type: MFA.QUESTION_ANSWER
    };

    this.userService.disableMfa(data).then((response) => {

      this.profile_mfa = false;
      this.reconfigure = false;
      this.answers.forEach(function (answer) {
        answer.answer = '';
      });
      this.message = response.message.text;
      this.successModal.show();

    }).catch((err) => {

      this.message = err.error.message.text;
      this.errorModal.show();
    });
  }

  questionCount() {

    return Array(5);
  }

  reconfigureMfa() {

    this.reconfigure = true;
    this.answers.forEach(function (answer) {
      answer.answer = '';
    });
  }
}
