import { OnInit, Component, ViewEncapsulation, ViewChild } from '@angular/core';
import { ProfileService } from '../../profile/profile.service';
import { UserService } from '../../user.service';
import { SuccessModalComponent } from 'shared/components/success-modal/success-modal.component';
import { ErrorModalComponent } from 'shared/components/error-modal/error-modal.component';
import { MFA } from 'config/constants/user';
import { User } from 'core/providers/user.service';
import { ClipboardService } from 'ngx-clipboard';

@Component({
  selector: 'app-qr-code',
  templateUrl: 'qr-code.component.html',
  encapsulation: ViewEncapsulation.None,
  providers: [ProfileService, UserService]
})
export class QrCodeComponent implements OnInit {
  @ViewChild('successModal') successModal: SuccessModalComponent;
  @ViewChild('errorModal') errorModal: ErrorModalComponent;

  profile_mfa: boolean = false;
  reconfigure: boolean = false;

  qr_code: string;
  qr_code_loading: boolean = false;
  mfa_code: string;
  recovery_codes: string[];

  message: string;
  user_details: User;

  constructor(
    private profileService: ProfileService,
    private userService: UserService,
    private clipboardService: ClipboardService,
    private user: User) {

    this.user_details = user;
  }

  ngOnInit(): void {

    this.profileService.getProfile('mfa').then((response) => {

      if (response.mfa) {
        for (const mfa of response.mfa) {
          switch (mfa.mfa_type) {

            case MFA.QR_CODE:
              this.profile_mfa = true;
              this.recovery_codes = response.recovery_codes;
              break;
          }
        }
      }

      if (!this.profile_mfa) {
        this.initiate();
      }
    });
  }

  initiate() {

    const self = this;
    this.qr_code_loading = true;

    this.userService.initiateMfa(MFA.QR_CODE).then((response) => {

      self.qr_code = response.qr_code;
      self.qr_code_loading = false;

    }).catch((err) => {

      self.message = err.error.message.text;
      self.errorModal.show();
    });
  }

  activate() {

    const data: any = {
      mfa_type: MFA.QR_CODE,
      code: this.mfa_code
    };

    this.userService.enableMfa(data).then((response) => {

      if (response.recovery_codes) {
        this.recovery_codes = response.recovery_codes;
      }
      this.profile_mfa = true;
      this.reconfigure = false;
      this.message = response.message.text;
      this.successModal.show();

    }).catch((err) => {

      this.message = err.error.message.text;
      this.errorModal.show();
    });
  }

  deactivate() {

    const data: any = {
      mfa_type: MFA.QR_CODE
    };

    this.userService.disableMfa(data).then((response) => {

      this.profile_mfa = false;
      this.reconfigure = false;
      this.mfa_code = '';
      this.initiate();
      this.message = response.message.text;
      this.successModal.show();

    }).catch((err) => {

      this.message = err.error.message.text;
      this.errorModal.show();
    });
  }

  downloadRecoveryCodes() {

    if (!this.recovery_codes || this.recovery_codes.length <= 0) {
      return;
    }

    let download_text = 'ExaTask Recovery Codes (' + this.user.email_id + ')\n\n';
    for (const code of this.recovery_codes) {
      download_text += code + '\n';
    }

    const download_file_name = 'exatask-recovery-codes.txt',
      donwload_file = new Blob([download_text], { type: 'text' });

    if (window.navigator && window.navigator.msSaveOrOpenBlob) {
      window.navigator.msSaveOrOpenBlob(donwload_file, download_file_name);
    } else {
      const download_link = document.createElement('a');
      download_link.href = URL.createObjectURL(donwload_file);
      download_link.download = download_file_name;
      document.body.appendChild(download_link);
      download_link.click();
      document.body.removeChild(download_link);
    }
  }

  copyRecoveryCodes() {

    let copy_text = '';
    for (const code of this.recovery_codes) {
      copy_text += code + '\n';
    }

    this.clipboardService.copyFromContent(copy_text);
  }

  reconfigureMfa() {

    this.reconfigure = true;
    this.initiate();
  }
}
